  .row {
	color: white;
	margin-left: 20px;
  }
  
  .row__posters {
	display: flex;
	overflow-y: hidden;
	overflow-x: scroll;
	padding: 20px;
  }
  
  .row__posters::-webkit-scrollbar {
	display: none;
  }
  
  .row__poster {
	/* object-fit: contain; */
	margin-right: 10px;
	/* max-height: 25px; */
	/* width: 100%; */
	/* transition: transform 450ms; */
  }
  
  .row__poster:hover {
	transform: scale(1.08);
	opacity: 1;
  }
  
  .trailer {
	/* padding: 3rem; */
	position: relative;
	z-index: 1;
	display: grid;
	place-items: center;
  }
  
  .list__items > .list__item {
	  flex: 0 0 300px;
	  /* height: 200px; */
	  background: #fff;
	  border-radius: 1rem;
	  overflow: hidden;
	  position: relative;
	  box-shadow: rgba(0, 0, 0, 0.2) 0 2px 4px -1px, rgba(0, 0, 0, 0.14) 0 4px 5px 0, rgba(0, 0, 0, 0.12) 0 1px 10px 0;
	  cursor: pointer;
  }
  
  .list__items > .list__item + .list__item {
	  margin-left: 1rem;
  }
  
  .list__item > img {
	  width: 100%;
	  height: 100%;
	  transform: scale(1.1);
	  transition: all 0.2s ease-out;
	  object-fit: cover;
  }
  
  .list__item:hover > img {
	  transform: scale(1);
  }
  
  .list__item::before {
	  position: absolute;
	  content: '';
	  width: 100%;
	  height: 100%;
  
	  top: 0;
	  left: 0;
	  transition: all 0.2s;
	  z-index: 1;
  }
  
  .list__item:hover::before {
	  background-position: 0% 100%;
  }
  
  .list__itemInfo {
	  position: absolute;
	  left: 0;
	  bottom: -6rem;
	  padding: 1rem;
	  z-index: 9;
	  width: 100%;
	  transition: bottom 0.2s ease-in;
  }
  
  .list__item:hover .list__itemInfo {
	  bottom: 0;
  }
  
  .list__itemTitle {
	  white-space: nowrap;
	  text-overflow: ellipsis;
	  overflow: hidden;
	width: 95%;
	color: white;
	  margin-bottom: 1.25rem;
	  transition: margin-bottom 0.2s ease-out;
  }
  
  .list__item:hover .list__itemTitle {
	  margin-bottom: 0.5rem;
  }
  
  .list__itemYear {
	  font-size: 0.85rem;
	  margin-left: 0.5rem;
	  font-family: "Work Sans", sans-serif;
	  font-weight: 400;
	  opacity: 0.75;
  }
  
  .list__itemOverview {
	  height: 2.4rem;
	  overflow: hidden;
  }
  
  .list__item .movieRating {
	  transform-origin: 0 0;
	  transform: scale(0.9);
	  margin-left: -4px;
  }